import React, { useState, useEffect } from "react";
import Lottie from "react-lottie";
import { Howl } from "howler";

import * as loading from "./loading.json";
import "./App.scss";
import Loading from "./Loading";
import Top from "./components/header/Top";
import SynthFoot from "./components/body/synthwave";

const defaultOptions = {
  loop: true,
  autoplay: true,
  animationData: loading.default,
  rendererSettings: { preserveAspectRatio: "xMidYMid slice" },
};

function App() {
  const [active, setActive] = useState(0);

  const sound = new Howl({
    src: ["https://tomsza26.github.io/portfolio2/audio/migawka.wav"],
  });
  const sound2 = new Howl({
    src: ["https://tomsza26.github.io/portfolio2/audio/katana.wav"],
  });
  const sound3 = new Howl({
    src: ["https://tomsza26.github.io/portfolio2/audio/Blizzard.mp3"],
    loop: true,
    volume: 0.3,
  });

  useEffect(() => {
    if (active === 1) {
      const loadingCenter = document.querySelector("#loadingCenter");
      const superHot = document.querySelector(".SUPERHOT");
      const super1 = document.querySelector("#super1");
      const super2 = document.querySelector("#super2");
      const super3 = document.querySelector("#super3");
      const super4 = document.querySelector("#super4");
      const super5 = document.querySelector("#super5");
      const super6 = document.querySelector("#super6");
      loadingCenter.style.animation =
        "fadeInOut 0.7s linear 0.5s 1 normal forwards";
      document.querySelector(".App").style.animation =
        "colorAnim 0.7s linear 0.5s 1 normal forwards";
      setTimeout(() => {
        loadingCenter.parentNode.removeChild(loadingCenter);
        superHot.style.display = "flex";
        super1.style.animation = "superAnimation 0.3s linear 1 normal forwards";
        sound.play();

        setTimeout(() => {
          super1.parentNode.removeChild(super1);
          super2.style.display = "flex";
          document.querySelector("#first1").style.animation =
            "superAnimation2 0.3s linear 1 normal forwards";
          document.querySelector("#first2").style.animation =
            "superAnimation 0.3s linear 1 normal forwards";
          document.querySelector("#first3").style.animation =
            "superAnimation2 0.3s linear 1 normal forwards";
          sound.play();

          setTimeout(() => {
            super2.parentNode.removeChild(super2);
            super3.style.display = "flex";
            super3.style.animation =
              "superAnimation 0.3s linear 1 normal forwards";
            sound.play();

            setTimeout(() => {
              super3.parentNode.removeChild(super3);
              super4.style.display = "flex";
              super4.style.animation =
                "superAnimation 0.3s linear 1 normal forwards";
              sound.play();

              setTimeout(() => {
                super4.parentNode.removeChild(super4);
                super5.style.display = "flex";
                super5.style.animation =
                  "superAnimation 0.3s linear 1 normal forwards";
                sound.play();

                setTimeout(() => {
                  super5.parentNode.removeChild(super5);
                  super6.style.display = "flex";
                  super6.style.animation =
                    "superAnimation 0.3s linear 1 normal forwards";
                  sound.play();

                  setTimeout(() => {
                    const blade = document.querySelector(".blade");
                    superHot.style.animation =
                      "fillIn 4s linear 1 normal forwards";

                    setTimeout(() => {
                      blade.style.display = "flex";
                      sound2.play();
                      const mainBody = document.querySelector(".mainBody");

                      setTimeout(() => {
                        mainBody.style.display = "block";
                        document.querySelector("#bodyUp").style.animation =
                          "cubeRewerse 1s normal forwards";
                        document.querySelector("#bodyDown").style.animation =
                          "cubeRewerse2 1s normal forwards";
                        superHot.parentNode.removeChild(superHot);
                        document.querySelector(".mainBody").style.position =
                          "relative";
                        document.querySelector("html").style.overflowY =
                          "initial";
                        SynthFoot();

                        setTimeout(() => {
                          document.querySelector(".equalizer").style.display =
                            "inline-block";
                          sound3.play();
                        }, 1000);
                      }, 500);
                    }, 3000);
                  }, 500);
                }, 170);
              }, 170);
            }, 170);
          }, 1200);
        }, 900);
      }, 1800);
    }
  });

  const musicPlay = () => {
    if (sound3.playing()) {
      sound3.pause();
      document.querySelector(".eq1").style.animationName = "none";
      document.querySelector(".eq2").style.animationName = "none";
      document.querySelector(".eq3").style.animationName = "none";
    } else {
      sound3.play();
      document.querySelector(".eq1").style.animationName = "shorteq";
      document.querySelector(".eq2").style.animationName = "talleq";
      document.querySelector(".eq3").style.animationName = "shorteq";
    }
  };

  return (
    <div className="App">
      <button className="equalizer" type="button" onClick={musicPlay}>
        <span className="eq1" />
        <span className="eq2" />
        <span className="eq3" />
      </button>
      <div className="mainBody">
        <div id="bodyUp">
          <div id="upFront" className="d3">
            <Top />
          </div>
          <div id="upBack" className="d3" />
        </div>
        <div id="bodyDown">
          <div id="downFront" className="d3" />
          <div id="downBack" className="d3">
            <Loading sendData={setActive} />
          </div>
        </div>
      </div>
      <div id="loadingCenter">
        <Lottie options={defaultOptions} height={120} width={120} />
      </div>
      <div className="SUPERHOT">
        <div className="superS" id="super1">
          GOD DAMN
        </div>
        <div className="superS" id="super2">
          <div id="first1">ARE YOU</div>
          <div id="first2">FAST</div>
          <div id="first3">ENOUGH</div>
        </div>
        <div className="superS" id="super3">
          I
        </div>
        <div className="superS" id="super4">
          DON'T
        </div>
        <div className="superS" id="super5">
          THINK
        </div>
        <div className="superS" id="super6">
          SO
        </div>
      </div>
      <div className="blade">
        <div className="bladeP" />
      </div>
    </div>
  );
}

export default App;
