/* eslint-disable no-nested-ternary */
import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faGithub } from '@fortawesome/free-brands-svg-icons';

import './Footer.scss';

function Footer() {
  const [status, setStatus] = useState('');

  const submitForm = (ev) => {
    ev.preventDefault();
    const form = ev.target;
    const data = new FormData(form);
    const xhr = new XMLHttpRequest();
    xhr.open(form.method, form.action);
    xhr.setRequestHeader('Accept', 'application/json');
    xhr.onreadystatechange = () => {
      if (xhr.readyState !== XMLHttpRequest.DONE) return;
      if (xhr.status === 200) {
        form.reset();
        setStatus('SUCCESS');
      } else {
        setStatus('ERROR');
      }
    };
    xhr.send(data);
  };

  return (
    <footer>
      <div id="canv">
        <div id="footCont">
          <div className="feedback-card">
            {status === '' ? (
              <>
                <div className="feedback-header">
                  CONTACT ME
                  <br />
                  we can build amazing things together
                </div>
                <form
                  className="feedback-body"
                  action="https://formspree.io/mnqberay"
                  onSubmit={submitForm}
                  method="POST"
                >
                  <input
                    type="email"
                    name="email"
                    required
                    className="feedback-body__email"
                    placeholder="Email"
                  />
                  <textarea
                    type="text"
                    name="message"
                    required
                    className="feedback-body__message"
                    placeholder="Message"
                    minLength="10"
                    maxLength="300"
                  />
                  <button type="submit">SEND</button>
                </form>
              </>
            ) : status === 'SUCCESS' ? (
              <div className="feedback-header">
                A message has been sent.
                <br />
                Thanks!
              </div>
            ) : (
              <div className="feedback-header">
                Oops! There has been an error.
                <br />
                Try again later.
              </div>
            )}
          </div>
        </div>
      </div>
      <div className="theLastStand">
        <a
          className="gitLink"
          href="https://github.com/tomsza26"
          target="_blank"
          rel="noopener noreferrer"
        >
          <FontAwesomeIcon icon={faGithub} id="gitHub" color="#f05133" />
          <span id="spanGit">My GitHub</span>
        </a>
        <div className="inspiredBy">
          <div className="insBy">INSPIRED BY:</div>
          <a
            href="https://superhotgame.com/"
            target="_blank"
            rel="noopener noreferrer"
            className="aIns"
          >
            SUPERHOT
          </a>
          <a
            href="https://hotlinemiami.com/"
            target="_blank"
            rel="noopener noreferrer"
            className="aIns"
          >
            HOTLINE MIAMI
          </a>
        </div>
        <div className="copyright">Copyright Tomasz Szablewski 2020 &copy;</div>
      </div>
    </footer>
  );
}

export default Footer;
