import React from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faExternalLinkAlt, faCode } from '@fortawesome/free-solid-svg-icons';

function singleProject(props) {
  const { desc, name, url, homepage } = props;

  return (
    <div className="projectBlock">
      <header className="projName">{name}</header>
      <div className="imgCont">
        <img
          src={require('../../images/projectsImg/' + name + '.gif') || ''}
          alt="projectImg"
          className="projImg"
        />
        <div className="projDesc">{desc || 'No description here.'}</div>
      </div>
      <div className="plinks">
        {homepage ? (
          <a
            href={homepage}
            target="_blank"
            rel="noopener noreferrer"
            className="aLinks"
          >
            <FontAwesomeIcon
              icon={faExternalLinkAlt}
              color="#ff4ba8"
              className="iconL"
            />
            <span className="codeN">live</span>
          </a>
        ) : (
          ''
        )}
        <a
          href={url}
          target="_blank"
          rel="noopener noreferrer"
          className="aLinks"
        >
          <FontAwesomeIcon icon={faCode} color="#ff4ba8" className="iconL" />
          <span className="codeN">code</span>
        </a>
      </div>
      <div className="forSmaller">{desc}</div>
    </div>
  );
}

export default singleProject;
