import React from 'react';

import getRepos from './gitHubRepos';
import './Projects.scss';
import Project from './singleProject';

function Projects() {
  const [data, loading] = getRepos();

  return (
    <div id="pContainer">
      <div id="pHeader">my projects</div>
      <div id="pProjects">
        {loading
          ? ''
          : data.map((item) => (
              <Project
                key={item.id}
                desc={item.description}
                name={item.name}
                url={item.html_url}
                homepage={item.homepage}
              />
            ))}
      </div>
    </div>
  );
}

export default Projects;
